.top_header{
    display: grid;
    grid-template-areas: "a b c";
    grid-template-columns: 1fr 1fr 1fr;
    background: linear-gradient(36deg, #616c6e, #252b31, #051520);
    width: 100%;
    justify-content: center;
    justify-items: center;
}
.social{
    padding-top: 30px;
    grid-area:a;
    grid-template-columns: 1fr 1fr;
   justify-items: center;
    
}
.telegram{
    padding-top: 20px;
}
.logo{
   grid-area: b;  
   justify-content: center; 
   
}
.graficlogo{
    padding: 5px;
    width: 150px;
    height: 150px;
    border-radius: 100px;
    max-width: 100%;
    box-sizing: border-box;
    justify-content: center;
}
.phone a{
    color: rgb(182, 26, 72);
    justify-content: center;
    text-decoration-color: none;
    text-decoration-line: none;
}
.viber:hover{
    color: blanchedalmond;
}
p:hover{
    color: blanchedalmond;
}
.phone{
    color: rgb(182, 26, 72);
}
